<script>
import axios from 'axios';



export default {
  data() {
    return {
      // Aquí puedes definir tus datos si los necesitas
    };
  },
  async mounted() {
    const IPINFO_API_URL = 'https://ipinfo.io/json';
    const IPINFO_API_TOKEN = '593e4000e493cf'; // Reemplaza esto con tu token de API de ipinfo.io
    try {
      const response = await axios.get(IPINFO_API_URL, {
        params: {
          token: IPINFO_API_TOKEN,
        },
      });

      const { country } = response.data;

      // Verificar si la IP pertenece a Chile, Brasil o Argentina
      if (country !== 'CL' && country !== 'BR' && country !== 'AR') {
        // Redireccionar a invitro.com.ar
        window.location.href = 'https://invitro.com.ar/banco-de-semen/';
      }
    } catch (error) {
      console.error('Error fetching geolocation data:', error);
      // Aquí puedes manejar el error si lo deseas
    }
  },
  created() {
    // Este gancho de ciclo de vida puede ser eliminado si no se usa
  },
};
</script>

